import React from 'react';
import logo from './logo.svg';
import FileSaver from 'file-saver';
import CsvFile1 from './resources/LA_County_101pts.csv';
import CsvFile2 from './resources/SIDE_5000_Random_Pts_ipr1317.csv';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Simply put the files you want to test downloading on a VM in <code>resources/file-name</code> and save to reload.
        </p>
        <button
        onClick={
          FileSaver.saveAs(
            CsvFile1,
          'LA_County_101pts.csv'
        )}
        >
          Click here to download or 'Save As' <code>LA County 101 points</code>
        </button>
        <button
        onClick={
          FileSaver.saveAs(
            CsvFile2,
          'SIDE_5000_Random_Pts_ipr1317.csv'
        )}
        >
          Click here to download or 'Save As' <code>SIDE 5000 random points</code>
        </button>
      </header>
    </div>
  );
}

export default App;
